/**
 * 不包含ploatform
 */
import HttpRequest from './axios.operative'
import { runEnv } from './util'
import config from '../config'
sessionStorage.setItem('currentEnv', runEnv())
const Axios = new HttpRequest({
  // baseURL: baseURL,
  baseURL: `${config.baseUrl[runEnv()]}/`,
  withCredentials: true, // 跨域请求时发送 cookies
  timeout: 30000 // request timeout
})
export default Axios
