<template>
  <div class="generate-qr-code">
    <div class="generate-qr-code-heander">
      <h1>生成兼职二维码</h1>
    </div>
    <div class="search-container">
      <Form class="form-container" :model="formData" :label-width="100" inline>
        <FormItem label="兼职姓名">
          <Input
            placeholder="请输入兼职姓名"
            clearable
            :maxlength="15"
            v-model="formDataSearch.name"
            class="iptWidth"
          >
          </Input>
        </FormItem>
        <FormItem label="手机号">
          <Input
            placeholder="请输入手机号"
            clearable
            v-model="formDataSearch.phone"
            class="iptWidth"
          >
          </Input>
        </FormItem>
        <FormItem label="创建人">
          <Input
            placeholder="请输入创建人"
            clearable
            v-model="formDataSearch.createEmpName"
            class="iptWidth"
          >
          </Input>
        </FormItem>
        <FormItem class="btnArea">
          <Button class="searchBtn" type="primary" @click="handleSearchContent"
            >查询</Button
          >
          <Button type="default" @click="handleAddItem">新增</Button>
        </FormItem>
      </Form>
    </div>
    <div class="qr-code-table">
      <Table
        class="qr-code-tabl-content"
        border
        :loading="tableLoading"
        :columns="qrCodeColumns"
        :data="qrCodeData"
      ></Table>
      <Row type="flex" justify="end">
        <Page :total="totalCount"  @on-change="onChangePage"/>
      </Row>
    </div>
    <Modal v-model="qrCodeModal" title="新增兼职二维码">
      <Form class="form-container" :model="formData" :label-width="100" inline>
        <FormItem label="兼职姓名" required>
          <Input
            placeholder="请输入兼职姓名"
            v-model="formData.name"
            :maxlength="15"
            @on-change="handleNameChange"
            class="iptWidth"
          >
          </Input>
        </FormItem>
        <FormItem label="手机号" required>
          <Input
            placeholder="请输入手机号"
            @on-change="handlePhoneChange"
            v-model="formData.phone"
            class="iptWidth"
          >
          </Input>
        </FormItem>
        <FormItem label="分校：" required>
          <!--:disabled="allCityList.length === 1"-->
          <Select
            v-model="formData.cityCode"
            filterable
            :label-in-value="true"
            style="width: 120px"
            @on-change="cityChange"
          >
            <Option
              v-for="(option, index) in allCityList"
              :value="option.id"
              :key="index"
              >{{ option.name }}</Option
            >
          </Select>
        </FormItem>
        <FormItem label="区域：" required>
          <Select
            v-model="formData.areaId"
            @on-change="changeArea"
            style="width: 120px"
            filterable
            :label-in-value="true"
          >
            <Option
              v-for="item in areaList"
              :value="item.schoolId"
              :key="item.schoolId"
              >{{ item.name }}</Option
            >
          </Select>
        </FormItem>
        <FormItem label="服务中心：" required>
          <Select
            v-model="formData.serviceCenterId"
            style="width: 120px"
            @on-change="changeServiceCenter"
            :label-in-value="true"
            filterable
          >
            <Option
              v-for="(item, index) in serviceCenterList"
              :value="item.schoolId"
              :key="index"
              >{{ item.name }}</Option
            >
          </Select>
        </FormItem>
      </Form>
      <div slot="footer">
        <Button
          type="primary"
          size="large"
          long
          :disabled="isSaveButtonDisable"
          :loading="saveLoading"
          @click="handleSaveInfo"
          >保存</Button
        >
      </div>
    </Modal>
  </div>
</template>
<script>
import BaseDataMixin from '@/mixins/baseData'
import { getPartTimePersonQRCode, updatePartTimePersonStatus, addPartTimePersonInfo, getPartTimePersonList } from '@/api/bd'

export default {
  mixins: [BaseDataMixin],
  data () {
    return {
      serviceCenterList: [],
      areaList: [],
      isSaveButtonDisable: true,
      saveLoading: false,
      qrCodeModal: false,
      tableLoading: false,
      formData: {
        name: '',
        phone: ''
      },
      formDataSearch: {
        name: '',
        phone: '',
        cityCode: '',
        areaId: '',
        cityName: '',
        areaName: '',
        serviceCenterName: '',
        serviceCenterId: '',
        createEmpNo: '',
        createEmpName: ''
      },
      qrCodeColumns: [
        {
          title: '兼职姓名',
          align: 'center',
          key: 'name'
        },
        {
          title: '手机号',
          align: 'center',
          key: 'phone'
        },
        {
          title: '当前状态',
          align: 'center',
          key: 'status',
          render: (h, params) => {
            return h('span', {}, params.row.status === 1 ? '停用' : '启用')
          }
        },
        {
          title: '所属服务中心',
          align: 'center',
          key: 'serviceCenterName'
        },
        {
          title: '创建时间',
          align: 'center',
          key: 'createTime'
        },
        {
          title: '创建人',
          align: 'center',
          key: 'createEmpName'
        },
        {
          title: '操作',
          align: 'center',
          key: 'action',
          fixed: 'right',
          width: 120,
          render: (h, params) => {
            return h('div', [
              params.row.status === 0 ? h(
                'Button',
                {
                  props: {
                    type: 'error',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.hanldeUpdatePartTimePersonInfo(params.row)
                    }
                  }
                },
                '停用'
              ) : h(
                'Button',
                {
                  props: {
                    type: 'success',
                    size: 'small'
                  },
                  on: {
                    click: () => {
                      this.hanldeUpdatePartTimePersonInfo(params.row)
                    }
                  }
                },
                '启用'
              ),
              h(
                'Button',
                {
                  props: {
                    type: 'primary',
                    size: 'small'
                  },
                  style: {
                    marginLeft: '8px'
                  },
                  on: {
                    click: () => {
                      this.downloadPartTimeQRCode(params.row)
                    }
                  }
                },
                '生成图片'
              )
            ])
          }
        }
      ],
      pageSize: 10,
      pageNo: 1,
      totalCount: 0,
      qrCodeData: []
    }
  },
  mounted () {
    this.getCityListHandler()
    this.fetchPartTimePersonList()
  },
  methods: {
    handleSearchContent () {
      this.fetchPartTimePersonList()
    },
    handleAddItem () {
      this.qrCodeModal = true
      this.formData.name = ''
      this.formData.phone = ''
      this.formData.cityName = ''
      this.formData.cityCode = ''
      this.formData.areaName = ''
      this.formData.areaId = ''
      this.formData.serviceCenterId = ''
      this.formData.serviceCenterName = ''
      this.formData.createEmpNo = ''
      this.formData.createEmpName = ''
    },

    fetchPartTimePersonList () {
      this.tableLoading = true
      const req = {
        createEmpName: this.formDataSearch.createEmpName,
        name: this.formDataSearch.name,
        phone: this.formDataSearch.phone,
        pageNo: this.pageNo,
        pageSize: this.pageSize,
        cityCode: this.$store.state.roleRelativeData.userInfo.cityCode
      }
      getPartTimePersonList(req).then(res => {
        this.tableLoading = false
        this.qrCodeData = res.records || []
        this.totalCount = res.totalCount || 0
      }).catch(err => {
        this.$Message.error('获取兼职人员列表失败:' + err.message)
      })
    },
    hanldeUpdatePartTimePersonInfo (params) {
      const req = {
        partTimeUserId: params.id,
        status: params.status === 0 ? 1 : 0,
        empNo: this.$store.state.user.userInfo.id,
        empName: this.$store.state.user.userInfo.name
      }
      updatePartTimePersonStatus(req).then(res => {
        if (res) {
          this.$Message.success({ content: '更新兼职人员状态成功 ', duration: 5 })
        } else {
          this.$Message.error({ content: '更新兼职人员状态失败: ', duration: 5 })
        }
        this.fetchPartTimePersonList()
      }).catch(err => {
        this.$Message.error('更新兼职人员状态失败:' + err.message)
      })
    },
    handleAddPartTimePersonInfo () {
      const req = {
        name: this.formData.name,
        phone: this.formData.phone,
        status: 0,
        cityCode: this.formData.cityCode,
        cityName: this.formData.cityName,
        areaId: this.formData.cityCode,
        areaName: this.formData.cityName,
        serviceCenterId: this.formData.serviceCenterId,
        serviceCenterName: this.formData.serviceCenterName,
        createEmpNo: this.$store.state.user.userInfo.id,
        createEmpName: this.$store.state.user.userInfo.name
      }
      addPartTimePersonInfo(req).then(res => {
        if (res) {
          this.$Message.success({ content: '添加兼职人员状态成功 ', duration: 5 })
          this.fetchPartTimePersonList()
        } else {
          this.$Message.error({ content: '添加兼职人员状态失败: ' + res.message, duration: 5 })
        }
        this.qrCodeModal = false
      }).catch(err => {
        this.$Message.error('添加兼职人员状态失败:' + err.message)
      })
    },
    downloadPartTimeQRCode (params) {
      const req = {
        trigPageTag: 'libuPartTime',
        codeType: '2',
        partTimeUserId: params.id,
        partTimeUserName: params.name,
        serviceCenterId: params.serviceCenterId,
        serviceCenterName: params.serviceCenterName,
        empNo: this.$store.state.user.userInfo.id,
        empName: this.$store.state.user.userInfo.name
      }
      getPartTimePersonQRCode(req).then(res => {
        const link = document.createElement('a')
        let blob = new Blob([res.data], { type: res.data.type })
        let url = URL.createObjectURL(blob)
        link.href = url
        link.download = params.name + params.serviceCenterName + '.jpg'
        setTimeout(function () {
          link.click()
        }, 66)
      }).catch(err => {
        this.$Message.error('下载兼职人员二维码失败:' + err.message)
      })
    },
    onChangePage (pageNo) {
      this.pageNo = pageNo
      this.fetchPartTimePersonList()
    },
    handleSaveInfo () {
      this.handleAddPartTimePersonInfo()
    },
    cityChange (val) {
      this.formData.cityName = val.label || ''
      this.isSaveButtonDisable = !(this.formData.serviceCenterId && this.formData.areaId && this.formData.cityCode && this.formData.name && this.formData.phone)
      // 改变城市
      this.formData.areaId = ''
      this.formData.serviceCenterId = ''
      this.areaList = []
      this.serviceCenterList = []
      this.baseInfo = {}
      if (!val.value) return
      this.getAreaListHandler({
        cityCode: val.value
      })
        .then(res => {
          this.areaList = res
        //   queryServiceCenterByEmpNo({
        //     cityCode: this.formData.cityCode,
        //     empNo: this.$store.state.user.userInfo.id
        //   })
        //     .then(data => {
        //         console.log(data,'service')
        //   this.formData.areaId = data.areaId
        //   this.changeArea(data.areaId, data.serviceCenterId)
        // })
        // .catch(err => {
        //   this.$Message.error('获取当前所在服务中心失败:' + err.message)
        // })
        })
        .catch(err => {
          this.$Message.error('获取区域失败:' + err.message)
        })
    },
    changeServiceCenter (val) {
      this.isSaveButtonDisable = !(this.formData.serviceCenterId && this.formData.areaId && this.formData.cityCode && this.formData.name && this.formData.phone)
      this.formData.serviceCenterName = val.label
    },
    handleNameChange () {
      this.isSaveButtonDisable = !(this.formData.serviceCenterId && this.formData.areaId && this.formData.cityCode && this.formData.name && this.formData.phone)
    },
    handlePhoneChange () {
      this.isSaveButtonDisable = !(this.formData.serviceCenterId && this.formData.areaId && this.formData.cityCode && this.formData.name && this.formData.phone)
    },
    changeArea (val, serviceCenterId) {
      this.formData.areaName = val.label
      this.isSaveButtonDisable = !(this.formData.serviceCenterId && this.formData.areaId && this.formData.cityCode && this.formData.name && this.formData.phone)
      // 改变区域
      this.serviceCenterList = []
      this.formData.serviceCenterId = ''
      this.baseInfo = {}
      if (!val.value) {
        return false
      }
      let req = {
        cityCode: this.formData.cityCode,
        areaId: this.formData.areaId
      }
      this.getServiceCenterListHandler(req)
        .then(res => {
          this.serviceCenterList = res
          this.serviceCenterList.forEach(value => {
            if (!value.address) {
              value.address = ''
            }
          })
          if (serviceCenterId) {
            this.formData.serviceCenterId = serviceCenterId
          }
        })
        .catch(err => {
          this.$Message.error('获取服务中心失败:' + err.message)
        })
    }
  }
}
</script>
<style lang="less" scoped>
.generate-qr-code {
  padding: 24px;
  line-height: 1.5;
  width: 100%;
  .search-container {
    padding-top: 24px;
    background-color: #ffffff;
  }
  .qr-code-table {
    margin-top: 24px;
    .qr-code-tabl-content {
      margin-bottom: 24px;
    }
  }
}
</style>
